<template>
    <div
            class="input"
            :class="{'input-active': inputActive}"
    >
        <div class="input-placeholder">
            <slot></slot>
        </div>
        <input
                class="input-value"
                :type="type"
                ref="inputRef"
                :value="value"
                v-on:input="$emit('input', $event.target.value)"
                @focus="onFocus"
                @blur="onBlur"
        >
        <svg
                class="input-clear"
                :class="{'input-clear-show': value}"
                @click="clear"
        >
            <use xlink:href="#icon-clear"></use>
        </svg>
        <div class="input-bar"></div>
    </div>
</template>

<script>
  export default {
    name: "Input",
    props: {
      value: {
        type: String,
        default: ''
      },
      type: {
        type: String,
        default: 'text'
      }
    },
    data() {
      return {
        inputActive: false
      }
    },
    created() {
      this.firstBlur = true
      window.inputBlur = () => {
        if (this.firstBlur) {
          this.firstBlur = false
          this.blur()
        }
      }
      window.addEventListener('touchmove', window.inputBlur, {passive: true})
    },
    watch: {
      value: {
        immediate: true,
        handler: function (newValue) {
          this.value = newValue
          if(newValue) this.inputActive = true
        }
      }
    },
    methods: {
      onFocus() {
          this.inputActive = true
        this.firstBlur = true
        this.$emit('on-focus')
      },
      onBlur() {
        if (!(this.value && this.value.length > 0)) {
          this.inputActive = false
        }
        this.$emit('on-blur')
      },
      clear() {
        this.$emit('on-clear');
        this.$emit('input', '');
        this.focus();
      },
      focus() {
          this.$refs.inputRef.focus()
      },
      blur() {
        this.$refs.inputRef.blur()
      }
    }
  }
</script>

<style lang="scss" scoped>
    .input {
        position: relative;

        width: 100%;

        overflow: hidden;

        .input-placeholder {
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            align-items: center;

            color: rgba(0, 0, 0, 0.54);

            transform: translate3d(0, 15px, 0) scale(1);
            transform-origin: top left;
            transition: transform $time $function, color $time $function;
        }

        .input-value {
            margin-top: 12px;
            padding-right: 23px;

            width: 100%;
            height: 24px;

            font-size: 16px;

            box-sizing: border-box;
        }

        .input-clear {
            position: absolute;
            right: 0;
            top: 12px;

            padding: 4px;

            width: 16px;
            height: 16px;

            color: $theme-color;
            fill: currentColor;
            transform: scale(0);
            transition: transform $time $function;

            &.input-clear-show {
                transform: scale(1);
            }
        }

        .input-bar {
            height: 1px;

            background-color: $theme-color;

            transform: translate3d(-100%, 0, 0);
            transition: transform $time $function;
        }

        &.input-active {
            .input-placeholder {
                color: $theme-color;
                transform: translate3d(0, 0, 0) scale(0.75);
            }

            .input-bar {
                transform: translate3d(0, 0, 0);
            }
        }
    }
</style>